@font-face {
  font-weight: 800;
  font-family: "Montserrat";
  font-style: italic;
  font-display: swap;

  src:
    local("Montserrat ExtraBold Italic"),
    local("Montserrat-ExtraBoldItalic"),
    url("../fonts/montserrat-italic_800.woff2") format("woff2"),
    url("../fonts/montserrat-italic_800.woff") format("woff");
}

